import React from 'react';
import {FlexContainer} from "../../../components/FlexContainer";
import {SectionTitle} from "../../../components/SectionTitle";
import {Skill} from "./skill/Skill";
import {Container} from "../../../components/Container";
import {S} from "./Skills_Styles";
import {Fade} from "react-awesome-reveal";

const skillData = [
    {iconId:"code", title: "Html5", progress: "70%"},
    {iconId:"css", title: "Css3", progress: "70%"},
    {iconId:"react", title: "React", progress: "50%"},
    {iconId:"styledComponents", title: "styled components", progress: "60%"},
    {iconId:"typescript", title: "Type Script",progress: "60%"},
    {iconId:"figma", title: "Figma", progress: "80%"},
]

export const Skills: React.FC = () => {
    return (
        <S.Skills id="skills">
            <Container>
                <SectionTitle>My Skills</SectionTitle>
                <FlexContainer wrap={"wrap"} justify={"space-between"} align={"center"}>
                    <Fade cascade={true}
                          damping={0.2}>
                        {skillData.map((s, index) => {
                            return <Skill iconId={s.iconId}
                                          title={s.title}
                                          progress={s.progress}
                                          key={index}/>
                        })
                        }
                    </Fade>

                </FlexContainer>

            </Container>

        </S.Skills>
    );
};



