import {S} from "../HeaderMenu_Styles"
import React from "react";
import {Menu} from "../menu/Menu";
import {ItemsType} from "../../Header";

export const DesktopMenu: React.FC<{ menuItems: Array<ItemsType> }> = (props: { menuItems: Array<ItemsType> }) => {
    return (
        <S.DesktopMenu>
            <Menu menuItems={props.menuItems} />
        </S.DesktopMenu>
    );
};









