import React from 'react';
import {S} from "../HeaderMenu_Styles"
import {ItemsType} from "../../Header";

export const Menu: React.FC<{ menuItems: Array<ItemsType> }> = (props: { menuItems: Array<ItemsType> }) => {
    return (
        <S.ListMenu>
            {props.menuItems.map(
                (item, index) => {
                    return <S.ListItem key={index}>
                        <S.NavLink to={item.id}
                                   smooth={true}
                                   spy={true}
                                   activeClass="active">
                            {item.title}
                            <S.Mask>
                                <span>{item.title}</span>
                            </S.Mask>
                            <S.Mask>
                                <span>{item.title}</span>
                            </S.Mask>
                        </S.NavLink>
                    </S.ListItem>
                }
            )}
        </S.ListMenu>
    );
};



